<template>
  <div class="service-manage">
    <div class="service-manage__header">
      <el-input
        v-model.trim="searchName"
        class="service-manage__header__item service-manage__header__search"
        size="small"
        placeholder="输入服务名称搜索"
        @keyup.enter.native="handleSearch"
      />
      <el-button
        class="service-manage__header__item"
        type="primary"
        size="small"
        @click="handleSearchByName"
        >搜索</el-button
      >
      <el-button
        class="service-manage__header__item"
        type="primary"
        size="small"
        plain
        @click="handleReset"
        >重置</el-button
      >
    </div>
    <div class="service-manage__body">
      <div class="el-card-warpper service-manage__body__list">
        <el-card class="el-card--no-shadow el-card--header-nbb el-card--flex">
          <div slot="header">
            <span class="el-card__header__title">服务列表</span>
          </div>
          <el-table
            class="service-manage__body__list__table sbl-table"
            :data="tableDate"
          >
            <el-table-column
              align="center"
              label="服务编号"
              min-width="100"
              prop="id"
              fixed
            />
            <el-table-column
              align="center"
              label="服务名称"
              min-width="150"
              fixed
            >
              <template slot-scope="scope">
                <div class="sbl-table__name-col">
                  <span class="sbl-table__name-col__name">{{
                    scope.row.name
                  }}</span>
                  <el-tooltip
                    v-if="scope.row.remark"
                    class="color-576277"
                    effect="dark"
                    :content="scope.row.remark"
                    placement="top"
                  >
                    <i class="el-icon-info" />
                  </el-tooltip>
                </div>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="服务所需业务角色"
              min-width="150"
              prop="depenceOn"
            />
            <el-table-column
              align="center"
              label="首次开启日期"
              min-width="150"
            >
              <template slot-scope="scope">
                {{ scope.row.firstOpenDate }}
              </template>
            </el-table-column>
            <el-table-column align="center" label="当前状态" min-width="100">
              <template slot-scope="scope">
                <div
                  class="sbl-table__status-col"
                  @click="handleStatusColClick(scope)"
                >
                  <el-switch
                    v-model="scope.row.isOnServerStatus"
                    :value="scope.row.isOnServerStatus"
                    inactive-color="#C0C4CC"
                    active-color="#409EFF"
                    :width="60"
                    :class="
                      scope.row.isOnServerStatus
                        ? 'online-style'
                        : 'offline-style'
                    "
                    :active-text="scope.row.isOnServerStatus ? '' : ''"
                    :inactive-text="
                      !scope.row.isOnServerStatus ? '关闭' : '发布'
                    "
                  />
                </div>
              </template>
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              fixed="right"
              width="240"
            >
              <template
                slot-scope="scope"
                class="service-manage__body__list-handle"
              >
                <el-button type="text" @click="handleEditDesc(scope)"
                  >编辑描述</el-button
                >
                <el-button type="text" @click="handleSeeHandleConlog(scope)"
                  >操作日志</el-button
                >
              </template>
            </el-table-column>
          </el-table>
          <div class="service-manage__body__list__pagination">
            <el-pagination
              :current-page="page.currentPage"
              :page-sizes="pageSizes"
              :page-size="page.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
            />
          </div>
        </el-card>
      </div>
    </div>
    <el-dialog
      :visible.sync="serviceStatusDialog"
      :title="`${curServiceStatus ? '关闭' : '开启'}服务`"
      width="400px"
      top="25vh"
      center
      :before-close="cancelServerChange"
      @closed="handleResetForm('service-status-form')"
    >
      <el-form
        ref="service-status-form"
        class="service-manage__dialog-form"
        :model="serviceStatusForm"
        label-width="80px"
        label-position="right"
        :rules="serviceStatusFormRules"
        :append-to-body="true"
      >
        <el-form-item label="服务名称" prop="name">
          <el-input
            v-model.trim="serviceStatusForm.name"
            size="small"
            disabled
          />
        </el-form-item>
        <el-form-item label="服务描述" prop="remark">
          <el-input
            v-model="serviceStatusForm.remark"
            :autosize="{ minRows: 3, maxRows: 10 }"
            type="textarea"
            maxlength="200"
            show-word-limit
            disabled
          />
        </el-form-item>
        <el-form-item label="修改原因" prop="reason">
          <el-input
            v-model.trim="serviceStatusForm.reason"
            size="small"
            maxlength="10"
            show-word-limit
          />
        </el-form-item>
      </el-form>
      <div style="text-align:center;">
        <el-button type="primary" size="small" @click="confirmSSF"
          >确认{{ curServiceStatus ? '关闭' : '开启' }}</el-button
        >
        <el-button
          class="service-manage__dialog-form__cancel"
          size="small"
          @click="cancelServerChange"
          >取消并返回</el-button
        >
      </div>
    </el-dialog>
    <el-dialog
      :visible.sync="editDialogVisible"
      title="编辑服务描述"
      width="400px"
      top="25vh"
      center
      @closed="handleResetForm('edit-form')"
    >
      <el-form
        ref="edit-form"
        class="service-manage__dialog-form"
        :model="editForm"
        label-width="80px"
        label-position="right"
        :rules="editFormRules"
        :append-to-body="true"
      >
        <el-form-item label="服务名称" prop="name">
          <el-input v-model.trim="editForm.name" size="small" disabled />
        </el-form-item>
        <el-form-item label="服务描述" prop="remark">
          <el-input
            v-model="editForm.remark"
            :autosize="{ minRows: 3, maxRows: 10 }"
            type="textarea"
            maxlength="200"
            show-word-limit
          />
        </el-form-item>
      </el-form>
      <div style="text-align:center;">
        <el-button type="primary" size="small" @click="confirmEdit"
          >保存修改</el-button
        >
        <el-button
          class="service-manage__dialog-form__cancel"
          size="small"
          @click="cancelDialog"
          >取消并返回</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { systemRole } from '@/utils/dictionary'
import {
  getServices,
  changeServerStatus,
  updateServiceInfo
} from '@/api/service'
const defaultEditForm = {
  name: '',
  remark: ''
}

const defaultServiceStatusForm = {
  name: '',
  remark: '',
  reason: ''
}

export default {
  data() {
    return {
      searchName: '',
      tableDate: [],
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 20, 50],
      editDialogVisible: false,
      editForm: Object.assign({}, defaultEditForm),
      editFormRules: {
        name: [{ required: true, message: '请输入服务名称', trigger: 'blur' }],
        remark: [{ required: true, message: '请输入服务描述', trigger: 'blur' }]
      },
      serviceStatusDialog: false,
      curServiceStatus: 'OPEN',
      curServiceIndex: 0,
      serviceStatusForm: Object.assign({}, defaultServiceStatusForm),
      serviceStatusFormRules: {
        reason: [{ required: true, message: '请输入修改原因', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.handleSearch()
  },
  methods: {
    systemRole,
    cancelDialog() {
      this.editDialogVisible = false
      this.handleReset()
    },
    cancelServerChange() {
      this.serviceStatusDialog = false
      this.handleReset()
    },
    setTableData({ list }) {
      this.tableDate = list.map((item, index) => ({
        id: item.id ? item.id : String(index),
        _id: item._id ? item._id : index,
        name: item.name ? item.name : '-',
        remark: item.remark ? item.remark : '-',
        depenceOn: item.depence_on ? this.systemRole(item.depence_on, '') : '-',
        firstOpenDate: item.first_server_at
          ? this.formatTime(item.first_server_at)
          : '-',
        isOnServerStatus: item.is_on_server ? item.is_on_server : false
      }))
    },
    setPage({ current_page: currentPage, pag_size: pageSize, count: total }) {
      this.page.currentPage = currentPage
      this.page.pageSize = pageSize
      this.page.total = total
    },

    handleSearchByName() {
      if (!this.searchName) {
        this.$message({
          message: '请输入服务名称搜索',
          type: 'error'
        })
        return
      }
      const pageQuery = {
        currentPage: 1,
        pageSize: 10
      }
      this.handleSearch(pageQuery)
    },

    async handleSearch(pageQuery) {
      const _query = pageQuery || {}
      if (!_query.currentPage) _query.current_page = this.page.currentPage
      if (!_query.pageSize) _query.page_size = this.page.pageSize
      _query.name = this.searchName
      const { data } = await getServices(_query)
      if (data.list.length === 0 && this.page.currentPage > 1) {
        this.handleSearch(
          Object.assign(_query, {
            currentPage: 1
          })
        )
        this.setPage({ currentPage: 1, pageSize: 10, total: 0 })
        return
      } else {
        this.setPage({ ...data })
      }
      this.setTableData({ ...data })
    },

    async handleReset() {
      this.searchName = ''
      await this.handleSearch()
    },

    handleEditDesc({ row }) {
      this.editForm = Object.assign({}, this.editForm, {
        name: row.name,
        _id: row._id,
        remark: row.remark
      })
      this.editDialogVisible = true
    },

    handleSeeHandleConlog({ row }) {
      this.$router.push({
        name: 'ServiceHandleConlog',
        params: { id: row._id },
        query: { serviceName: row.name }
      })
    },

    async handleCurrentChange(page) {
      this.page.currentPage = page
      await this.handleSearch()
    },
    async handleSizeChange(pageSize) {
      this.page.pageSize = pageSize
      await this.handleSearch()
    },
    handleResetForm(ref) {
      this.$refs[ref].resetFields()
      this.handleSearch()
    },
    confirmEdit() {
      this.$refs['edit-form'].validate(valid => {
        if (valid) {
          updateServiceInfo(this.editForm._id, {
            remark: this.editForm.remark
          }).then(res => {
            const data = res.data
            const operatorName = '保存修改'
            this.$message({
              message:
                data && data.result === true ? operatorName + '成功' : '失败',
              type: data && data.result ? 'success' : 'error'
            })
            if (res && res.data.result) {
              this.handleSearch()
            }
          })
          this.editDialogVisible = false
        } else {
          return false
        }
      })
    },
    handleStatusColClick({ row, $index }) {
      this.curServiceStatus = !row.isOnServerStatus
      this.curServiceIndex = $index
      this.serviceStatusForm = Object.assign({}, defaultServiceStatusForm, {
        name: row.name,
        remark: row.remark
      })
      this.serviceStatusDialog = true
    },
    async confirmSSF() {
      this.$refs['service-status-form'].validate(async valid => {
        if (valid) {
          const curOrderInfo = this.tableDate[this.curServiceIndex]
          curOrderInfo.is_on_server = !curOrderInfo.isOnServerStatus
          const { data } = await changeServerStatus(curOrderInfo._id, {
            is_on_server: !curOrderInfo.is_on_server,
            reason: this.serviceStatusForm.reason
          })
          this.serviceStatusDialog = false
          const operatorName = this.curServiceStatus ? '确认关闭' : '确认开启'
          this.$message({
            message:
              data && data.result === true ? operatorName + '成功' : '失败',
            type: data && data.result ? 'success' : 'error'
          })
          if (data && data.result) {
            this.handleSearch()
          }
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="scss">
.service-manage {
  .el-card__header {
    margin-bottom: 16px;
  }
  &__body {
    &__list {
      .el-card__body {
        height: calc(100% - 72px);
      }
      .color-576277 {
        color: #576277;
      }
    }
  }
  .el-form-item {
    &__label,
    &__content {
      line-height: 32px;
    }
  }

  &__dialog-form {
    textarea {
      resize: none;
    }
    .el-input__count {
      line-height: 1.5;
    }
  }
}
.el-tooltip__popper {
  max-width: 900px;
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.service-manage {
  min-height: 100%;
  &__header {
    overflow: hidden;
    &__item {
      float: left;
    }
    &__search {
      margin-right: 30px;
      width: 220px;
    }
  }
  &__body {
    padding-top: 30px;
    height: calc(100% - 32px);
    &__list {
      overflow: auto;
      height: 100%;
      .el-card {
        height: 100%;
      }
      .sbl-table {
        &__name-col {
          display: inline-flex;
          align-items: center;
          line-height: 0;
          &__name {
            padding-right: 10px;
          }
          &__icon {
            font-size: 18px;
            color: #5ba5e8;
            cursor: pointer;
          }
        }
      }
      &__pagination {
        padding: 60px 0 40px;
        text-align: center;
      }
    }
  }
  &__dialog-form {
    &__cancel {
      margin-left: 20px;
    }
  }
}
</style>
